import HeadImgItem from '@/components/content/defimglist/HeadImgItem.vue'


import { doPublishDynamic } from '@/network/dynamic.js'

export default {
	components: {
		HeadImgItem
	},
	data() {
		return {
			txt1: "",
			txt2: "",
			userAccount: '',
			currentText: ''
		}
	},
	methods: {
		//发表动态
		PublishDynamic() {
			if(this.currentText.length > 0) {
				const obj = {
					account: this.userAccount,
					content: this.currentText,
					address: '广州天河'
				}
				doPublishDynamic(obj).then(res => {
					console.log(res)
					if(res.status === 200) {
						if(res.data.code === 2000) {
							uni.$emit('updateDynamicList', {})
							uni.navigateBack()
						}else {
							uni.showToast({
								title: '发表失败',
								icon: 'none'
							})
						}
					}else {
						uni.showToast({
							title: '发表失败',
							icon: 'none'
						})
					}
				}).catch(err => {
					console.log(err)
				})
			}else {
				uni.showToast({
					title: '请输入内容',
					icon: 'none'
				})
			}
			
		},
		toLocationInfo() {
			uni.navigateTo({
				url: "/components/content/myLocation/LocationInfo"
			})
		}
	},
	onNavigationBarButtonTap(options) {
		if(options.index === 0) {
			console.log('option')
			this.PublishDynamic()
		}
	},
	//返回触发modal
	onBackPress(e) {
		if(e.from === 'navigateBack') {
			//返回
			return false
		}
		if(e.from === 'backbutton') {
			if(this.currentText.length > 0) {
				uni.showModal({
					title: '提示',
					content: '您确定退出当前编辑？',
					success(res) {
						if(res.confirm) {
							uni.navigateBack()
						}else if(res.cancel){
							//表示禁止默认返回
							return true
						}
					},
					fail(err) {
						console.log(err)
						return true
					}
				})
			}else {
				return false
			}
		}
		return true
	},
	created() {
		const userInfo = uni.getStorageSync('userInfo')
		this.userAccount = userInfo.user.userAccount
	}
}
